import React from 'react';

import './search-panel.css';

export default class SearchPanel extends React.Component {

  state = {
    term: '',
  }

  buttons = [
    {name: "all", label: 'All', className: 'search-panel__actions-all'},
    {name: 'active', label: 'Active', className: 'search-panel__actions-active'},
    {name: 'done', label: 'Done', className: 'search-panel__actions-done'},
  ]

  onSearch = e => {
    const value = e.target.value;
    this.setState({ term: value });
    this.props.onSearch(value);
  }
  render() {
    const buttons = this.buttons.map((item, index) => {
      const active = item.name === this.props.filter;
      const className = active ? item.className + ' active' : item.className;
      return (
        <button name={item.name} type="button" className={className} key={index} onClick={() => this.props.onFilterChange(item.name)}>{item.label}</button>
    )})

    return (
      <form className="search-panel">
        <input className="search-panel__input" type="search" placeholder="Search task" name="Search" value={this.state.term} onChange={this.onSearch} />
        <div className="search-panel__actions">
          {buttons}
        </div>
      </form>
    );
  }
}
