import React from 'react';

import './addItem.css';

class AddItem extends React.Component {

  state = {
    label: ''
  }

  onChangeInput = (e) => {
    this.setState({ label: e.target.value })
  }

  onSubmit = (e) => {
    e.preventDefault();
    const value = e.target['add-input'].value;
    this.props.onAddItem(value);
    this.setState({ label: '' });
  }

  render () {
    return (
      <form className="add-item" name="form-add" onSubmit={this.onSubmit}>
        <input type="text" className="add-input" name="add-input" placeholder="input tast text" value={this.state.label} onChange={this.onChangeInput}></input>
        <button type="button" type="submit" className="btn">{this.props.children}</button>
      </form>
    )} 
} 

export default AddItem;