import React from 'react';

import './todo-list-item.css';

export default class TotoListItem extends React.Component {

  render() {
    const { label, done, important } = this.props;
    let classNames = "todo-list-item";
    if (done) classNames += ' done';
    if (important) classNames += ' important';
    return (
      <div className={ classNames }>
        <span onClick={ this.props.onToggleDone }>{ label }</span><button className="delete" onClick={ this.props.onDeleted }><i className="fa fa-close"></i></button><button className="important-btn" onClick={ this.props.onToggleImportant }><i className="fa fa-exclamation"></i></button>
      </div>
    );
  }
}