import React from 'react';

import AppHeader from '../app-header/app-header';
import SearchPanel from '../search-panel/search-panel';
import TodoList from '../todo-list/todo-list';
import AddItem from '../addItem/addItem';

class App extends React.Component  {
  maxId = 100;
  
  state = {
    todoData: [
      this.createTodoItem('Drink Coffee'),
      this.createTodoItem('Learn React'),
      this.createTodoItem('Make Awesome App'),
    ],
    term: '',
    status: 'all'
  }

  createTodoItem(label) {
    return { label, important: false, done: false, id: this.maxId++ };
  }

  onDeleted = (id) => {
    console.log(id);
    this.setState(prevState => {
      const index = prevState.todoData.findIndex(item => item.id === id);
      const before = prevState.todoData.slice(0, index);
      const after = prevState.todoData.slice(index + 1);
      return { todoData: before.concat(after)}
    })
  }

  onAddItem = text => {
    const newItem = this.createTodoItem(text);
    this.setState(prevState => ({ 
      todoData: [
        ...prevState.todoData,
        newItem
      ]
     }));
  }

  toggleProperty(arr, id, propName) {
    const index = arr.findIndex(item => item.id === id);
    const newItem = { ...arr[index] , [propName]: !arr[index][propName] };
    return { todoData: [...arr.slice(0, index), newItem, ...arr.slice(index + 1)]};

  }

  onToggleDone = (id) => {
    this.setState(prevState => {
      const newState = this.toggleProperty(this.state.todoData, id, 'done')
      return newState;
    })
  }
  onToggleImportant = (id) => {
    this.setState(prevState => {
      const newState = this.toggleProperty(this.state.todoData, id, 'important')
      return newState;
    })
  }

  search(items, term) {
    console.log(items, term);
    if (term.length === 0) return items;
    return items.filter(item => item.label.toLowerCase().indexOf(term.toLowerCase()) > -1);
    }

  onSearch = (term) => this.setState({ term });

  filter(items, status) {
    switch(status) {
      case 'all':
        return items;
      case 'done':
        return items.filter(item => item.done);
      case 'active': 
        return items.filter(item => !item.done);
      default: 
        return items;
    }
  }

  onFilterChange = status => this.setState({ status });

  render() {
    
    const doneCount = this.state.todoData.filter(item => item.done).length;
    const todoCount = this.state.todoData.length - doneCount;
    const visibleItems = this.filter(this.search(this.state.todoData, this.state.term), this.state.status);
    return (
    <div className="wrapper">
      <AppHeader  done={doneCount} todo={ todoCount } />
      <SearchPanel onSearch={this.onSearch} filter={this.state.status} onFilterChange={this.onFilterChange} />
      <TodoList todos={ visibleItems }
                onDeleted={this.onDeleted}
                onToggleDone={this.onToggleDone}
                onToggleImportant={this.onToggleImportant} />
      <AddItem onAddItem={this.onAddItem}>Add new Item</AddItem>
    </div>
  )}
}

export default App;